.App {
  text-align: center;
}

.App-body {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.frame {
    height: 560px;
    width: 360px;
    position: relative;
    overflow: hidden;
}

.frame:after {
    content: '';
    position: absolute;
    top: 0;
    left: -10px;
    right: -10px;
    height: 20px;
    box-shadow: inset 0 6px 6px 0 black
}

.box {
    position: absolute;
    margin-bottom: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column-reverse;
}

.row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.stage_base  {
    width: 32px;
    height: 32px;
    line-height: 32px;
    margin: 0 5px;
    font-size: 12px;
    color: white;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.75);
}
.stage_0  { background-color: #50F246 }
.stage_1  { background-color: #49FC6F }
.stage_2  { background-color: #4EE695 }
.stage_3  { background-color: #49FCCB }
.stage_4  { background-color: #36F5F1 }
.stage_5  { background-color: #4BE4F2 }
.stage_6  { background-color: #4EC3FC }
.stage_7  { background-color: #5292E6 }
.stage_8  { background-color: #4E70FC }
.stage_9  { background-color: #433BF5 }
.stage_10 { background-color: #6733F2 }
.stage_11 { background-color: #9C35FC }
.stage_12 { background-color: #BF3BE6 }
.stage_13 { background-color: #FC35F7 }
.stage_14 { background-color: #F5228F }
.stage_15 { background-color: #F53702 }

.cell {
    width: 32px;
    height: 32px;
    line-height: 30px;
    text-align: center;
    box-sizing: border-box;
    background: darkgray;
    border-right-color: grey;
    border-bottom-color: grey;
    color: #282c34;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.25);
}

.cell_basic {
    background-color: darkolivegreen;
    background-image: url("assets/textures/TX Tileset Grass.png");
    background-position: -32px -32px;
    border-color: transparent;
    color: white;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
}

.cell_empty {}
.cell_hidden {}
.cell_visible {}
.cell_x_0 { background-position-x: -00px; }
.cell_x_1 { background-position-x: -32px; }
.cell_x_2 { background-position-x: -64px; }
.cell_x_3 { background-position-x: -96px; }
.cell_x_4 { background-position-x: -128px; }
.cell_x_5 { background-position-x: -160px; }
.cell_x_6 { background-position-x: -192px; }
.cell_x_7 { background-position-x: -224px; }
.cell_y_0 { background-position-y: -00px; }
.cell_y_1 { background-position-y: -32px; }
.cell_y_2 { background-position-y: -64px; }
.cell_y_3 { background-position-y: -96px; }
.cell_y_4 { background-position-y: -128px; }
.cell_y_5 { background-position-y: -160px; }
.cell_y_6 { background-position-y: -192px; }
.cell_y_7 { background-position-y: -224px; }

.cell_crater {}
.cell_dud    {}
.cell_blank  {}

.cell_visible:not(.cell_bomb) {}

.cell_background {
    width: 32px;
    height: 32px;
}
.cell_hidden .cell_backgroundX {
    background-color: black;
    opacity: 0.6;
}
.cell_hidden .cell_background {
    background-color: #303030;
    opacity: 0.7;
    background-image:  repeating-linear-gradient(45deg, #212121 25%, transparent 25%, transparent 75%, #212121 75%, #212121), repeating-linear-gradient(45deg, #212121 25%, #303030 25%, #303030 75%, #212121 75%, #212121);
    background-position: 0 0, 8px 8px;
    background-size: 16px 16px;
}
.cell_hidden .cell_backgroundX {
    background-color: rgba(0, 0, 0, 0.1);
    background-image: url("assets/textures/TX Plant.png");
    background-position: -96px -192px;
}
.cell_crater .cell_background {
    background-image: url("assets/textures/TX Props.png");
    background-position: -96px -288px;
}
.cell_dud .cell_background {
    background-image: url("assets/textures/TX Props.png");
    background-position: -96px -352px;
}
.cell_blank .cell_background {
    background-image: url("assets/textures/TX Props.png");
    background-position: -96px -416px;
}


